.gallery-wrap {
    width: 100%;
}

.imgs-wrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
}

.work-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 1px dashed #969696;
    transition: 0.3s;
    margin-bottom: 3.0rem;

    img {
        width: 40.0rem;
    }
}

.work-wrap:hover {
    box-shadow: 0px 6px 13px rgba(0, 0, 0, 0.32);
}

.media-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 1px dashed #969696;
    transition: 0.3s;
    margin-bottom: 3.0rem;

    img {
        width: 38.0rem;
    }
}

.work-caption {
    width: 38.0rem;
    height: 14.3rem;
    background-color: #EBEBEB;
    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 1.6rem;
    font-family: FZLanTingHei-DB-GBK;
    font-weight: 400;
    color: #7B7B7B;
    padding: 1rem;
}

.video-wrap {
    display: flex;
    align-items: center;
    justify-content: center;

}

.video-box-wrap {
    width: 94%;
    border: 1px dashed #969696;
}

.home-title {
    font-size: 3.2rem;
    font-family: FZLanTingHei-R-GBK;
    font-weight: bold;
    color: #C73C27;
    text-align: center;
    height: 20rem;
    line-height: 20rem;

    span {
        padding-bottom: 1rem;
        border-bottom: 0.5rem solid #C73C27;
    }
}